body{
  background-color: white
}
.poll-excerpt {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.25rem 0.25rem;
  border: 0;
  border-radius: 7px;
}
.poll-excerpt > h5  {
  color: #a17c03;
}
.table-excerpt{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.table-excerpt > div {
display: flex;
flex-direction: row;
width: 100%;
justify-content: center;
}
.table-excerpt  div > h5 {
  color: #a17c03;
}
.dashboard-block{
  display: flex;
  width: 100%;
  margin-top: 2rem;
  justify-content: center;
}
.table-block{
  width: 80%;


}

.dashboard-block > article {
  flex: 1;
}

.poll-list {
 display: flex;
 align-items: center;
 flex-direction: column;
 margin-top: 2rem;
 width: 100%;
 font-family: 'Oswald', sans-serif;
 font-weight: 500;
}

.poll-list > div > h4{
  color: #1e823c
}
.dash-numbers > a{
  outline: none;
  color: #111211;
  text-decoration: none
}
.dash-numbers > a > h2{
  font-size: 5rem;
}
.stripped-anchor{
  outline: none;
  color: #111211;
  text-decoration: none
}
.form-body{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 2rem;
  width: 100%;
  font-family: 'Oswald', sans-serif;
  font-weight: 500;
  opacity: 0;

}
.transformed-form-body{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 2rem;
  width: 100%;
  font-family: 'Oswald', sans-serif;
  font-weight: 500;
  opacity: 1;
  transition-property: opacity;
  transition-duration: 2s;
  transition-delay: 0.1s;
}

.form-body > h1 > p{
  font-weight: 200;
  font-size: 4rem;
}
.form-section{
  display: flex;
  justify-content: center;
  width:35%;
  margin-top: 2rem;

}
.poll-table{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Oswald', sans-serif;
}
.parsedobjects{
  display: flex;
  align-items:flex-start;
  flex-direction: column;
  position: relative;
  top:5px ;
  left:0px;
  z-index: 1030;
  width:30%
}
.parsedobjects-items{
  all:revert;
  text-align-last: center;
  background-color:#baf5ba;
  margin-bottom: 10px;
  border-radius: 10px;
  padding: 2px;
  font-size: 1rem;
  color: #fcfcfc;
  width: 50%;
  box-shadow: 3px 3px 3px #bfd6bf;
}
.parsedobjects span:hover{
  background-color: #95ed95;
}
input.form-control{
  caret-color: #399e39;
  font-weight:600;
  color: #399e39;
}
.App {

}
.carousel-inner{

}
.carousel-control-prev{
  width:3%;
}
.carousel-control-next{
  width:3%;
}

.leadtitle {
  display: block;
  margin-top: 2rem;
  text-transform: capitalize;
}
.leadtitle-table{
  text-transform: capitalize;
  color: #a10611;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.leadtitle > a , .leadtitle-table > a {
  color: #a10611;
  outline: none;
  text-decoration: none;
}

.poll-button{
  background-color: #5bb577;
  border: 0;
}

.spinner{
  display: flex;
  flex-direction: row;
  margin-top: 3rem;
  justify-content: center;
  width: 100%
}
a[class="dropdown-item"] > div > div > button {
    font-size: .700rem
}

div[class="blockMessageLogin"] > div > div > button {
    color: #0f9c08;
}
